@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300&family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300&family=Titillium+Web&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin:0;
  padding:0;
  background-color: #000023;
}


main {
  background-color: white;
  width: 50%;
  height: auto;
  min-height: 700px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
  border-radius: 25px;
  box-shadow: 0px 0px 18px white;
}

header {
  width: 100%;
  height: auto;
  text-align: center;
}

header h1 {
  color: white;
  font-family: 'Titillium Web', sans-serif;
  font-size: 48px;
  width: 100%;
}

header p {
  color:white;
  float: right;
  margin-right: 120px;
  margin-top:-75px;
}
#newtask-section{
    width: 97%;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid black;
}


#newtask-section p {
    padding-top:2%;
    margin-left: 2%;
    font-family: 'Cairo', sans-serif;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
    width: 15%;
}

#newtask-section p:hover{
    color: lightgreen;
}

#task-form{
    padding-bottom: 1%;
    
}

#task-form label {
    font-family: 'Cairo', sans-serif;
    font-weight: bold;
}



#task-form input {
    margin-left: 2.5%;
    margin-right: 3%;
    border-radius: 25px;
    height: 25px;
    border: solid 1px black;
    padding-left: 1%;
}

#task-form input:focus{
    outline: 1px solid lightskyblue;
    border: none;
}

#task-form input:valid {
    outline: 1px solid limegreen;
    border: none;
}

#task-form input::-webkit-input-placeholder, #task-form textarea::-webkit-input-placeholder{
    font-style: italic;
    
}

#task-form input:-ms-input-placeholder, #task-form textarea:-ms-input-placeholder{
    font-style: italic;
    
}

#task-form input::placeholder,#task-form textarea::placeholder{
    font-style: italic;
    
}

#task-form button {
    background-color: white;
    border-radius: 50%;
    width: auto;
    height: auto;
    border: none;
    cursor: pointer;
    float:right;
    margin-right: 120px;
    margin-top: -2px;
}

#task-form button img {
    width: 32px;

}


#textarea-label {
    float:left;
    margin-right: 2%;

}
#taskdesc-id{
    margin-top:2%;
    resize: none;
    border-radius: 25px;
    padding: 1%;
    font-family: 'Cairo', sans-serif;
    font-size: 14px;
    font-weight: bold;
}

#taskdesc-id:focus {
    outline: 1px solid lightskyblue;
    border: none;
}


.form-hide{
    display: none;
}

.form-show{
    display: block;
}


#nexttasksection-id{
    border-bottom: solid 1px black;
    width: 97%;
    margin-left: auto;
    margin-right: auto;
}

#nexttasksection-id h2 {
    color:gray;
    margin-left: 2%;
    font-family: 'Cairo', sans-serif;
    font-size: 20px;
    font-weight: bold;
}


#taskarticle-id{
    width: 97%;
    border: solid 1px black;
    border-radius: 15px;
    margin-bottom: 1%;
    padding-left: 1%;
    margin-left: auto;
    margin-right: auto;
}


#taskarticle-id p:nth-of-type(1){
    width: 15%;
    font-weight: bolder;
}

#taskarticle-id p:nth-of-type(2){
    margin-top: -35px;
    width: 15%;
    float: left;
}

#taskarticle-id p:nth-of-type(3){
    float: right;
    width: 10%;
    margin-top: -60px;
}
#taskarticle-id p:nth-of-type(4){
    float: right;
    width: 20%;
    margin-top: -35px;
}




#usermodalsection-id{
    background-color: white;
    width: 350px;
    padding-top:20px;
    padding-left: 20px;
    padding-right: 20px; 
    margin-left: auto;
    margin-right: auto;
    height: auto;
    min-height: 150px;
    margin-top: 250px;
    border-radius: 25px;
    box-shadow: 0px 0px 10px white;
    padding-bottom: 25px;
}


#currentuser-id input, #newuser-id input {
    border-radius: 25px;
    width: 180px;
    height: 25px;
    padding-left: 2%;
    margin-left: 15px;
    border: solid 1px black;
    margin-bottom: 10px;
}

#newuser-id input{
    width:145px !important;
}
#currentuser-id label, #newuser-id label  {
    font-family: 'Cairo', sans-serif;
    font-size: 18px;
    font-weight: bold;
}

#currentuser-id input:focus, #newuser-id input:focus{
    outline: 1px solid lightskyblue;
    border: none;
}

#currentuser-id {
    border-bottom: solid 1px black;
    width: 100%;
}


#newuser-id{
    margin-top: 20px;
}  



#accessbutton-id, #registerbutton-id{
    cursor: pointer;
    margin-bottom: 20px;
    display: block;
    margin-right: auto;
    margin-left: auto;
    border-radius: 25px;
    width: 150px;
    height: 30px;
    background-color: white;
    border: solid 1px lightskyblue;
    font-family: 'Cairo', sans-serif;
    font-weight: bold;
}


